import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { updateProject } from "../../projectSlice";
import { 
    Row,
    Col,
    Form,
    FormGroup,
    Label,
    Input,
    Button 
} from "reactstrap";

export default function Export({ closeModal }) {
    const projects = useSelector(state => state.project.projects);
    const currentProjectIndex = useSelector(state => state.project.currentProjectIndex);
    const currentProject = projects[currentProjectIndex];
    const dispatch = useDispatch();
    const [exportData, setExportData] = useState({
        name: currentProject.name,
        width: currentProject.width,
        height: currentProject.height
    });

    const handleChange = e => {
        setExportData({
            ...exportData,
            [e.target.name]: e.target.value
        });
    }

    const handleSubmit = e => {
        e.preventDefault();

        dispatch(
            updateProject({
                ...currentProject,
                exportData
            })
        );
    }

    return (
        <>
            <Col md="8">
                <object data="https://velickovicdj.github.io/files/CV_VELICKOVICDJ.pdf" type="application/pdf"></object>
            </Col>
            <Col md="4">
                <Form onSubmit={handleSubmit}>
                    <FormGroup row>
                        <Col>
                            <Label>Name</Label>
                            <Input type="text" defaultValue={currentProject.name} />
                        </Col>
                    </FormGroup>
                    <FormGroup row>
                        <Col>
                            <Label>Format</Label><br/>
                            <select>
                                <option>PDF</option>
                                <option>PNG</option>
                                <option>JPG</option>
                            </select>
                        </Col>
                    </FormGroup>
                    <FormGroup row>
                        <Col>
                            <Label>Width</Label>
                            <Input 
                                type="text"
                                name="width"
                                value={exportData.width}
                                onChange={handleChange}
                            />
                        </Col>
                        <Col>
                            <Label>Height</Label>
                            <Input 
                                type="text" 
                                name="height"
                                value={exportData.height}
                                onChange={handleChange}
                            />
                        </Col>
                    </FormGroup>
                    <Row>
                        <Col><p className="small text-muted">File size: 141 KB</p></Col>
                    </Row>
                    <FormGroup row>
                        <Col md="6">
                            <Button className="w-100" color="secondary" onClick={closeModal}>Cancel</Button>
                        </Col>
                        <Col md="6">
                            <Button type="submit" className="w-100" color="primary">Export</Button>
                        </Col> 
                    </FormGroup>
                </Form>
            </Col>
        </>
    );
}